import React, { useState } from "react";
import LazyShow from "../components/LazyShow.js";
import dino from "../img/dino.png";
import todo from "../img/todo.png";
import api from "../img/api.png";

const Projects = ({ t, i18n }) => {
  const skills = [
    {
      name: "Fun Facts Api",
      src: api,
      desc: "With this API in one simple click you can discover wonderful facts about several different topics.",
      descPt:
        "Com esta API em um simples clique voc pode descobrir fatos impressionantes sobre vrios tpicos diferentes.",
      techstack: ["Node.js", "express", "MongoDB"],
      link: "https://funfactsapi.herokuapp.com/",
      repo: "https://github.com/ddvital/Fun-facts-api",
    },
    {
      name: "Dino game",
      src: dino,
      desc: "The Dinosaur Game (also known as the Chrome Dino) is a browser game developed by Google and built into the Google Chrome web browser. The player guides a pixelated Tyrannosaurus rex across a side-scrolling landscape, avoiding obstacles to achieve a higher score.",
      descPt:
        "O joguinho do dinossauro (tambm conhecido como Chrome Dino)  um jogo de navegador desenvolvido pelo Google e integrado ao navegador Google Chrome. O jogador guia um Tyrannosaurus rex pixelizado por uma paisagem de rolagem lateral, evitando obstculos para alcanar uma pontuao mais alta.",
      techstack: ["Javascript", "css", "html"],
      link: "https://ddvital-dinogame.herokuapp.com/",
      repo: "https://github.com/ddvital/Dino-game",
    },
    {
      name: "ToDo App",
      src: todo,
      desc: "Do you have an idea? use this ToDo App. Whether you want to increase your productivity, reduce your stress levels, or just free up some mental space, ToDo makes it easy to plan your day and manage your life.",
      descPt:
        "Voc tem alguma ideia? use este aplicativo ToDo. Se voc deseja aumentar sua produtividade, reduzir seus nveis de estresse ou apenas liberar algum espao mental, o ToDo facilita o planejamento do seu dia e o gerenciamento da sua vida.",
      techstack: ["React", "Sass"],
      link: "https://todo-app-ddvital.herokuapp.com/",
      repo: "https://github.com/ddvital/Todo-App",
    },
  ];

  return (
    <section className="projects">
      <LazyShow>
        <p className="section-title">{t("projects")}</p>
        <p className="subtitle">{t("projectsText")}</p>
      </LazyShow>
      <ul className="projects__list">
        {skills.map((skill) => {
          return (
            <LazyShow key={skill.name}>
              <li className="card">
                <div className="card__info">
                  <h2>{skill.name}</h2>
                  {i18n.language === "en" ? (
                    <div className="card__main">{skill.desc}</div>
                  ) : (
                    <div className="card__main">{skill.descPt}</div>
                  )}
                  <ul className="card__techstack">
                    {skill.techstack.map((item) => {
                      return <li>{item}</li>;
                    })}
                  </ul>
                  <div className="card__links">
                    <a href={skill.repo}>
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 24 24"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          id="icon"
                          d="M20.116 5.901C20.0348 5.79322 19.948 5.68975 19.856 5.591C19.9485 5.34444 20.0188 5.09009 20.066 4.831C20.2053 3.8832 20.0843 2.91535 19.716 2.031C19.716 2.031 18.596 1.681 16.026 3.411C14.9349 3.10974 13.8079 2.95835 12.676 2.961C11.5408 2.95901 10.4106 3.11038 9.316 3.411C6.746 1.661 5.626 2.031 5.626 2.031C5.26208 2.90598 5.14115 3.863 5.276 4.801C5.32345 5.07087 5.39713 5.33545 5.496 5.591C5.406 5.691 5.316 5.801 5.236 5.901C4.48978 6.83702 4.09367 8.00414 4.116 9.201C4.11362 9.48486 4.12698 9.76862 4.156 10.051C4.476 14.481 7.426 15.511 10.236 15.831C9.83828 16.2008 9.56859 16.6877 9.46599 17.221C9.37305 17.5767 9.32931 17.9434 9.336 18.311V19.621C8.217 19.721 7.06899 19.558 6.713 18.56C6.33085 17.6048 5.69707 16.7709 4.879 16.147C4.81921 16.1146 4.76233 16.0772 4.709 16.035C4.63716 15.8462 4.50989 15.6836 4.3439 15.5684C4.17792 15.4533 3.98099 15.3911 3.779 15.39H3.774C3.50964 15.39 3.25604 15.4947 3.06865 15.6811C2.88126 15.8676 2.77532 16.1207 2.774 16.385C2.771 17.197 3.584 17.722 3.917 17.9C4.30605 18.292 4.61901 18.7528 4.84 19.259C5.204 20.282 6.269 21.837 9.30599 21.635L9.30799 21.733L9.312 22.001C9.312 22.2662 9.41735 22.5206 9.60489 22.7081C9.79243 22.8956 10.0468 23.001 10.312 23.001H15.026C15.2912 23.001 15.5456 22.8956 15.7331 22.7081C15.9206 22.5206 16.026 22.2662 16.026 22.001C16.026 22.001 16.034 18.841 16.034 18.311C16.0407 17.9434 15.9969 17.5767 15.904 17.221L15.902 17.215L15.906 17.221C15.897 17.186 15.884 17.158 15.874 17.124C15.7626 16.6285 15.5047 16.178 15.134 15.831L15.146 15.852L15.126 15.832C17.936 15.512 20.866 14.462 21.186 10.052C21.215 9.76962 21.2284 9.48585 21.226 9.202C21.2437 8.00751 20.852 6.84295 20.116 5.902V5.901Z"
                          fill="#4961dc"
                        />
                      </svg>
                    </a>
                    <a href={skill.link}>
                      <svg
                        width="24"
                        height="24"
                        viewBox="0 0 24 24"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          id="icon"
                          d="M18 19H6C5.45 19 5 18.55 5 18V6C5 5.45 5.45 5 6 5H11C11.55 5 12 4.55 12 4C12 3.45 11.55 3 11 3H5C4.46957 3 3.96086 3.21071 3.58579 3.58579C3.21071 3.96086 3 4.46957 3 5V19C3 20.1 3.9 21 5 21H19C20.1 21 21 20.1 21 19V13C21 12.45 20.55 12 20 12C19.45 12 19 12.45 19 13V18C19 18.55 18.55 19 18 19ZM14 4C14 4.55 14.45 5 15 5H17.59L8.46 14.13C8.27302 14.317 8.16798 14.5706 8.16798 14.835C8.16798 15.0994 8.27302 15.353 8.46 15.54C8.64698 15.727 8.90057 15.832 9.165 15.832C9.42943 15.832 9.68302 15.727 9.87 15.54L19 6.41V9C19 9.55 19.45 10 20 10C20.55 10 21 9.55 21 9V4C21 3.45 20.55 3 20 3H15C14.45 3 14 3.45 14 4Z"
                          fill="#4961dc"
                        />
                      </svg>
                    </a>
                  </div>
                </div>
                <div class="card__img">
                  <a href={skill.link}>
                    <img src={skill.src} alt="" />
                  </a>
                </div>
              </li>
            </LazyShow>
          );
        })}
      </ul>
    </section>
  );
};

export default Projects;
