import logo from "../img/logo.svg";
import LazyShow from "../components/LazyShow.js";
import arrow from "../img/arrow.svg";
import hero from "../img/hero.svg";

function Hero({ t, i18n }) {
  return (
    <header className="header">
      <nav className="header__nav">
        <img src={logo} alt="logo" />
        <ul>
          <li>
            <a href="#" className="nav__items">
              {t("introduction")}
            </a>
          </li>
          <li>
            <a href="#" className="nav__items">
              {t("aboutMe")}
            </a>
          </li>
          <li>
            <a href="#" className="nav__items">
              {t("projects")}
            </a>
          </li>
          <li>
            <a href="#" className="nav__items">
              {t("contact")}
            </a>
          </li>
        </ul>

        <button
          className="lang-switcher"
          onClick={() =>
            i18n.changeLanguage(i18n.language === "en" ? "pt" : "en")
          }
        >
          {i18n.language === "pt" ? "PT-BR" : "EN-US"}
        </button>
      </nav>

      <LazyShow>
        <div className="header__content">
          <div className="">
            <p className="section-title">{t("introduction")}</p>
            <h1>{t("heroH1")}</h1>
            <p className="header__text">{t("heroText")}</p>
          </div>
        </div>
      </LazyShow>

      <div className="scroll-down">
        <img src={arrow} alt="arrow down" className="scroll-down__arrow" />
        <div>{t("Scroll Down")}</div>
      </div>

      <div className="header__code hero-rect">
        <img src={hero} alt="rect" className="svg-rect" />
      </div>
    </header>
  );
}

export default Hero;
