import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    debug: true,
    fallbackLng: "pt",
    interpolation: {
      escapeValue: false, // not needed for react as it escapes by default
    },
    resources: {
      en: {
        translation: {
          changeLang: "Change Language",
          introduction: "Introduction",
          heroH1: "Hello, my name is David.",
          heroText:
            "Im a software Developer specializing in building (and occasionally designing) exceptional digital experiences. Currently, Im focused on building some personal projects.",
          scrollDown: "Scroll Down",
          aboutMe: "About Me",
          aboutText0:
            'Hello! My name is David and I enjoy creating things that live on the internet. My interest in Programming started back in 2019 when I decided to Start learning programming - I started with Python but my first "Hello, world" was in Pascal.',
          aboutText1:
            "Fast-forward to today, My main skills are Python, Javascript, C/C++, TypeScript, React, React native, Node.js, Express, Django, Flask, HTML, CSS, Sass, tailwind, Bootstrap, MongoDB, MySQL, and MariaDB.",
          aboutText2:
            "I'm also studying for a bachelor's degree in computer science at the Federal Technological University of Paran(UTFPR) and focusing on personal projects.",
          resume: "Resum",
          downloadCv: "Download CV.",
          projects: "Projects",
          projectsText:
            "I enjoy creating wonderful, and useful experiences for the end user. here is a small gallery of my recent projects.",
          contact: "Contact",
          sayHi: "Say Hi",
          letsWork: "Let's Work Together.",
          contactText:
            "Id love to meet up with you to discuss your venture, and potential collaborations.",
          name: "Name",
          msg: "Message",
          letsTalk: "Let's Talk",
          submitMsg:
            "Your message has been successfully sumitted, i'll be in touch shortly :)",
        },
      },
      pt: {
        translation: {
          changeLang: "Mudar Idioma",
          introduction: "Introduo",
          heroH1: "Ol, meu nome  David.",
          heroText:
            "Eu sou um desenvolvedor de software especializado em desenvolver (e ocasionalmente design) excelentes experiencias. atualmente, estou focado em projetos pessoais.",
          scrollDown: "Deslize para baixo",
          aboutMe: "Sobre Mim",
          aboutText0:
            'Ol! Meu nome  David e eu gosto de criar coisas que vivem na internet. Meu interesse por programao comeou em 2019, quando decidi comear a aprender programao - comecei com Python, mas meu primeiro "Hello, world" foi em Pascal.',
          aboutText1:
            "Avanando para os dias atuais, minhas principais habilidades so Python, Javascript, C/C++, TypeScript, React, React native, Node.js, Express, Django, Flask, HTML, CSS, Sass, tailwind, Bootstrap, MongoDB, MySQL e MariaDB.",
          aboutText2:
            "Atualmente estou cursando bacharelado em Cincia da Computao na Universidade Tecnolgica Federal do Paran (UTFPR) e focando em projetos pessoais.",
          resume: "Crriculo",
          projects: "Projetos",
          projectsText:
            "Eu amo Criar experiencias digitais incriveis. Abaixo tem uma lista dos meus projetos recentes.",
          contact: "Contato",
          sayHi: "Diga Oi",
          letsWork: "Vamos Trabalhar Juntos.",
          contactText:
            "Eu adoraria me encontrar com voc para discutir seu empreendimento, e possveis colaboraes.",
          name: "Nome",
          msg: "Mensagem",
          letsTalk: "Vamos Conversar",
          submitMsg:
            "Sua mensagem foi enviada com sucesso! em alguns minutos entrarei em contato :)",
        },
      },
    },
  });

export default i18n;
