import LazyShow from "../components/LazyShow.js";
import user from "../img/user.svg";
import paperPlane from "../img/paper-plane.svg";
import msg from "../img/msg.svg";
import arrowRight from "../img/arrow-right.svg";
import { useEffect, useState } from "react";
import axios from "axios";

function Hero({ t }) {
  const [message, setMessage] = useState("Hi, I saw your portfolio");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const URL = "https://ddvitalportfolio.herokuapp.com/";

  const loadApi = async () => {
    await axios.get(URL);
  };

  const sendMsg = async () => {
    let msgContent = {
      name: name,
      email: email,
      msg: message,
    };

    await axios.post(`${URL}api/message`, msgContent);
    alert(t("submitMsg"));
  };

  useEffect(() => {
    loadApi();
  }, []);

  return (
    <section className="contact">
      <LazyShow>
        <p className="section-title">{t("sayHi")} </p>
      </LazyShow>
      <div className="contact__main">
        <ul className="contact__info">
          <LazyShow>
            <h2 className="title">{t("letsWork")}</h2>
            <p className="subtitle contact__subtitle">{t("contactText")}</p>
          </LazyShow>
        </ul>
        <LazyShow>
          <ul className="contact__form">
            <li>
              <input
                type="text"
                name="name"
                className="form"
                placeholder={t("name")}
                autoComplete="off"
                onChange={(e) => setName(e.target.value)}
              />
              <img src={user} alt="form icon" className="form__icon" />
            </li>
            <li>
              <input
                type="email"
                name="email"
                className="form"
                placeholder="Email"
                autoComplete="off"
                onChange={(e) => setEmail(e.target.value)}
              />
              <img src={paperPlane} alt="form icon" className="form__icon" />
            </li>
            <li>
              <textarea
                name="message"
                placeholder={t("msg")}
                cols="30"
                autoComplete="off"
                rows="6"
                onChange={(e) => setMessage(e.target.value)}
              ></textarea>
              <img src={msg} alt="form icon" className="form__icon" />
            </li>
            <li>
              <button type="submit" className="submit-btn" onClick={sendMsg}>
                {t("letsTalk")}
                <div>
                  <img src={arrowRight} alt="" />
                </div>
              </button>
            </li>
          </ul>
        </LazyShow>
      </div>
    </section>
  );
}

export default Hero;
